<template>
  <div>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0">
        POINTS
      </v-tab>
      <v-tab :key="1">
        SERVER
      </v-tab>
      <v-tab :key="2">
        Debug Messages
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <bac-net-point-table ref="bacnetTable"></bac-net-point-table>
      </v-tab-item>
      <v-tab-item :key="1">
        <bacnet-server-form></bacnet-server-form>
      </v-tab-item>
      <v-tab-item :key="2">
        <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
      </v-tab-item>
    </v-tabs-items>

  </div>
</template>

<script>
import BacnetServerForm from "./BacnetServerForm";
import BacNetPointTable from "./BacNetPointTable";
import MqttConsole from '../Mqtt/MqttConsole';
import axios from "../../axios";

export default {
  components: {
    BacnetServerForm,
    BacNetPointTable,
    MqttConsole
  },
  data: () => ({
    tab: 0,
    topic: 'rubix/bacnet_server/debug',
    tabs: ['table', 'server', 'debug-message'],
  }),
  computed: {
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
  },
  watch: {
    '$route.query.tab' : function(val){
      this.tab = this.tabs.indexOf(val);
    },
    tab: function (val, oldVal) {
      if(oldVal == 0){
        this.$refs.bacnetTable.prepareToExit();
      }
      if(val == 0){
        this.$refs.bacnetTable.autoRefresh();
      }
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'bacnet-server', query: { tab: tabKey } });
      }
    },
  },
  beforeRouteLeave (to, from, next) {
    this.$refs.bacnetTable.prepareToExit();
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted(){
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
};
</script>
